import React from 'react';
import {
    createEnumParam,
    NumberParam,
    StringParam,
    useQueryParams
} from "use-query-params";
import {useAppDispatch, useAppSelector} from "../app/hooks";
import {selectControlFields} from "./selectors";
import {selectYear, setYear} from "../features/year/yearSlice";
import {map, filter, pickBy} from 'ramda';
import {selectPlotType, setPlotType} from "../features/plotType/plotTypeSlice";
import {setControls} from "../features/controls/controlsSlice";
import {allValuesNotNull} from "./utils/domain";

const Url = () => {
    const controls = useAppSelector(selectControlFields);
    const storedYear = useAppSelector(selectYear);
    const plotType = useAppSelector(selectPlotType);
    const dispatch = useAppDispatch();

    const [query, setQuery] = useQueryParams({
        x: StringParam,
        y: StringParam,
        z: StringParam,
        area: StringParam,
        compare: StringParam,
        plot: createEnumParam(['line', 'bubble', 'bar', 'map']),
        year: NumberParam
    });

    React.useEffect(() => {
        const { x, y, z, compare, plot, year } = query;
        const nonNullControls = filter(v => !!v, {x, y, z, compare});
        dispatch(setControls(nonNullControls));
        dispatch(setPlotType(plot));
        dispatch(setYear(year));
    }, [dispatch]);  // Only run 1st time, so query isn't a dependency

    React.useEffect(() => {
        if (allValuesNotNull(controls)) {
            const allParams = {
                plot: plotType ? plotType.name : null,
                ...map(f => f ? f.id : null, controls),
                year: storedYear
            };
            const params = filter(p => !!p, allParams);
            setQuery(params, 'pushIn');
        }
    }, [dispatch, query, setQuery, controls, storedYear, plotType]);

    return null;
};

export default Url;
