import {zipObj, mergeDeepWith, concat, intersection, reduce, tail, all, range, map} from 'ramda';

// bind ::  [a] -> (a -> [b]) -> [b]
const bind = xs => f => xs.flatMap(f);

// foldr :: (a -> b -> b) -> b -> [a] -> b
const foldr = f => a => xs =>
    xs.reduceRight((a, x) => f(x)(a), a);

export const cartProdN = foldr(
    xs => as =>
        bind(as)(
            x => bind(xs)(
                a => [
                    [a].concat(x)
                ]
            )
        )
)([
    []
]);

export type ObjWithId = {id: string};

export function normalize<T extends ObjWithId>(objArray: T[]) : { [key: string]: T } {
    return zipObj(objArray.map(item => item.id), objArray);
}

export const mergeDeepWithConcat = mergeDeepWith((x, y) => Array.isArray(x) ? concat(x, y) : y);

export const intersectAll = (xs) => reduce(intersection, xs[0], tail(xs));

export const getNestedValues = (obj: Object, level: number = 1) : any[] => {
    const vals = Object.values(obj);
    if (level > 1)
        return vals.flatMap(v => getNestedValues(level - 1, v));
    else
        return vals.flatMap(v => Object.values(v));
}

export const invertMapOfMaps = (
    obj: {[key1: string]: {[key2: string]: any[]}}
) : {[key2: string]: {[key1: string]: any[]}} => {
    const outerKeys = Object.keys(obj);
    if (outerKeys.length === 0)
        return {}
    const arrayLength = Object.values(obj[outerKeys[0]]).length;
    if (!all(k => Object.values(obj[k]).length === arrayLength, outerKeys))
        throw new Error("All arrays must be the same length.");
    const r = range(0, arrayLength);
    const innerKeys = Object.keys(obj[outerKeys[0]]);
    const vals = r.map(i => zipObj(outerKeys, outerKeys.map(k => obj[k][innerKeys[i]])));
    return zipObj(innerKeys, vals);
}

export const invertNestedObject = x => {
    return x == null || Object.values(x)[0] == null ? null :
        Object.entries(x).reduce((acc, [innerKey, obj]) => {
            Object.entries(obj).forEach(([outerKey, val]) => {
                acc[outerKey] = acc[outerKey] || {}; // grab the object already stored at the `outerKey` or create a new one.
                acc[outerKey][innerKey] = val;
            })
            return acc;
        }, {});
};
