import React from "react";
import {animated, useSpring} from 'react-spring';
import {extent} from 'd3-array';
import {scaleLinear, scalePower} from "@visx/scale";
import useSelect from "../hooks/useSelect";
import {useAppSelector} from "../app/hooks";
import {selectYear} from "../features/year/yearSlice";
import {selectCurrentDivisions} from "./selectors";
import {getNestedValues} from "./utils/misc";
import {prepareData} from "./utils/domain";

const paddingPct = 0.1;

export type BubblePlotProps = {
    data: any
    showLabel?: string
    disableInteraction?: boolean
    width: number
    height: number
};

const DumbBubblePlot = ({
    data,
    showLabel = null,
    disableInteraction = false,
    width,
    height,
    year,
    divisions
}) => {
    const scaleFactor = Math.min(width, height) / 100;
    const padding = Math.min(width, height) * paddingPct;
    const xScale = scaleLinear<number>({
        domain: extent(getNestedValues(data.byField.x)),
        range: [padding, width - padding],
        round: true
    });
    const yScale = scaleLinear<number>({
        domain: extent(getNestedValues(data.byField.y)),
        range: [height - padding / 2, padding],
        round: true
    });
    const sizeScale = scaleLinear<number>({
        domain: extent(getNestedValues(data.byField.z)),
        range: [scaleFactor, scaleFactor * 10]
    });
    const items = prepareData(data.byYear, year)
        .map(([k, v]) => ({
            ...divisions[k],
            ...v,
            scaledX: xScale(v.x),
            scaledY: yScale(v.y),
            size: sizeScale(v.z)
        }));

    const {makeSelectableAndInteractive, Tooltip} =
        useSelect(items, ['name'], showLabel, disableInteraction);
    return (
        <>
            <svg width={width} height={height}>
                {items.map(item => {
                    // eslint-disable-next-line react-hooks/rules-of-hooks
                    const [{animX, animY, animSize}, setAnim] = useSpring(() => ({
                        animX: item.scaledX,
                        animY: item.scaledY,
                        animSize: item.size
                    }));

                    setAnim({
                        animX: item.scaledX,
                        animY: item.scaledY,
                        animSize: item.size
                    })

                    return makeSelectableAndInteractive([item.name],
                        <animated.circle
                            cx={animX}
                            cy={animY}
                            r={animSize}
                            fill={'#3188f6EE'}
                            stroke={'#3188f6'}
                        />
                    );
                })}
            </svg>
            <Tooltip />
        </>
    );
};

const BubblePlot = (props: BubblePlotProps) => {
    const year = useAppSelector(selectYear);
    const divisions = useAppSelector(selectCurrentDivisions);
    return year && divisions &&
        <DumbBubblePlot {...props} year={year} divisions={divisions} />
};

export default BubblePlot;
