import {ToggleButton, ToggleButtonGroup} from "@mui/lab";
import {PLOT_TYPES} from "../../dashboard/config";
import React from "react";
import {useAppDispatch, useAppSelector} from "../../app/hooks";
import {selectPlotType, setPlotType} from "./plotTypeSlice";


const PlotTypeSelect = () => {
    const plotType = useAppSelector(selectPlotType);
    const dispatch = useAppDispatch();


    const changePlotType = (event, name) => {
        dispatch(setPlotType(name));
    }

    return (
        <div className='plot-type'>
            <ToggleButtonGroup size='small'
                               value={plotType.name}
                               exclusive
                               onChange={changePlotType}>
                {PLOT_TYPES.map((pt) => (
                    <ToggleButton key={pt.name} value={pt.name}>
                        {<pt.icon/>}
                    </ToggleButton>
                ))}
            </ToggleButtonGroup>
        </div>
    );
};

export default PlotTypeSelect;
