import {PLOT_TYPES_MAP} from "../../dashboard/config";
import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { RootState } from '../../app/store';

export interface PlotTypeState {
    value: string;
    status: 'idle' | 'loading' | 'failed';
}

const initialState: PlotTypeState = {
    value: 'bar',
    status: 'idle',
};

export const plotTypeSlice = createSlice({
    name: 'plotType',
    initialState,
    // The `reducers` field lets us define reducers and generate associated actions
    reducers: {
        // Use the PayloadAction type to declare the contents of `action.payload`
        setPlotType: (state, action: PayloadAction<string>) => {
            const value = action.payload;
            if (value != null && value !== '')
                state.value = value;
        },
    }
});

export const { setPlotType } = plotTypeSlice.actions;
export const selectPlotType = (state: RootState) => PLOT_TYPES_MAP[state.plotType.value];
export default plotTypeSlice.reducer;
