import {
    createAsyncThunk,
    createSelector,
    createSlice,
    PayloadAction
} from '@reduxjs/toolkit';
import { RootState } from '../../app/store';
import {Data, DataField, fieldCfgs, FieldType} from "../../dashboard/config";
import {mergeDeepWithConcat} from "../../dashboard/utils/misc";
import {map} from 'ramda';

