//         // The value we return becomes the `fulfilled` action payload
//         return response.data;
//     }
// );

export interface DataState {
    value: Data,
    status: 'idle' | 'loading' | 'failed';
}

const initialState: DataState = {
    value: {
        levels: {},
        areas: {},
        fields: {},
        years: {byLevelAndField: {}},
        divisions: {byLevel: {}},
        values: {byFieldAndDivision: {}}
    },
    status: 'idle',
};

const augmentField = (f: DataField) : DataField =>
    ({...f, ...fieldCfgs[f.id]});

const processData = (data: Data) : Data => {
    if (data.fields) {
        return {
            ...data,
            fields: map(augmentField, data.fields)
        }
    } else {
        return data;
    }
};

export const dataSlice = createSlice({
    name: 'data',
    initialState,
    // The `reducers` field lets us define reducers and generate associated actions
    reducers: {
        // Use the PayloadAction type to declare the contents of `action.payload`
        setData: (state, action: PayloadAction<Data>) => {
            const data = processData(action.payload);
            state.value = mergeDeepWithConcat(state.value, data);
        },
    }
});

export const { setData } = dataSlice.actions;
export const selectData = (state: RootState) => state.data.value;
export const selectAreas = (state: RootState) => state.data.value?.areas;
export const selectFields = (state: RootState) => state.data.value?.fields;
export const selectLevels = (state: RootState) => state.data.value?.levels;
export const selectYearsByLevelAndField = (state: RootState) =>
    state.data.value?.years.byLevelAndField;
export const selectDivisionsByLevel = (state: RootState) =>
    state.data.value?.divisions.byLevel;
export const selectValuesByFieldAndDivision = (state: RootState) =>
    state.data.value?.values.byFieldAndDivision;

export const selectNumericFields = createSelector(
    selectData,
    (data: Data) : DataField[] => {
        return data.fields ? Object.values(data.fields)
            .filter(f =>
                f.type === FieldType.NUMERIC ||
                f.type === FieldType.CURRENCY
            ) : null
    }
);

export default dataSlice.reducer;
