import React from 'react';
import { geoCentroid, geoAlbersUsa } from 'd3-geo';
import {animated, useSpring} from 'react-spring';
import { AlbersUsa } from '@visx/geo';
import * as topojson from 'topojson-client';
import topology from '../data/usa-topo.json';
import {extent} from 'd3-array';
import {zipObj, toPairs} from "ramda";
import { hcl } from 'd3-color';
import { scaleLinear } from 'd3-scale';
import { interpolateHcl } from 'd3-interpolate';
import {useAppSelector} from "../app/hooks";
import {selectYear} from "../features/year/yearSlice";
import useSelect from "../hooks/useSelect";
import {DataField} from "./config";
import {selectCurrentDivisions} from "./selectors";
import {getNestedValues} from "./utils/misc";

export const borderColor = '#FFF';

export type MapPlotProps = {
    width: number
    height: number
    data: any
    yAxisVar: DataField
    showLabel?: string
    disableInteraction?: boolean
};

interface FeatureShape {
    type: 'Feature';
    id: string;
    geometry: { coordinates: [number, number][][]; type: 'Polygon' };
    properties: { name: string };
}

// @ts-ignore
const { features: unitedStates } = topojson.feature(topology, topology.objects.states) as {
    type: 'FeatureCollection';
    features: FeatureShape[];
};
const projection = geoAlbersUsa();

const opacityOptions = {
    selectedOpacity: 1,
    unselectedOpacity: 0.2,
    defaultOpacity: 1
};

const DumbMapPlot = ({ data, width, height, year, divisions,
                  showLabel = null, disableInteraction = false }) => {
    const centerX = width / 2;
    const centerY = height / 2;
    const scale = Math.round(Math.min(width, height) * 1.4);

    const colorScale = scaleLinear()
        .domain(extent(getNestedValues(data.byField.y)))
        // @ts-ignore
        .range([
            hcl('rgba(41,122,204,0.1)'),
            hcl('#0265c9')])
        // @ts-ignore
        .interpolate(interpolateHcl)

    const items = toPairs(data.byYear[year].y)
        .map(([k, y]) => ({
            ...divisions[k],
            id: divisions[k]['geoid'].slice(-2),
            y,
            year,
            color: colorScale(y),
        }));
    const includeIds = items.map(d => d.id);
    const itemMap = zipObj(items.map(d => d.id), items);

    const ifs = unitedStates
        .filter(f => includeIds.includes(f.id))
        .map((f) => {
            const c = projection(geoCentroid(f));
            return {
                ...itemMap[f.id],
                scaledX: c[0] / 1.55,
                scaledY: c[1] / 1.55
            };
        });

    const {makeSelectableAndInteractive, Tooltip} =
        useSelect(ifs, ['name'], showLabel, disableInteraction);

    return width < 10 ? null : (
        <>
            <svg width={width} height={height}>
                <AlbersUsa<FeatureShape>
                    data={unitedStates}
                    scale={scale}
                    translate={[centerX, centerY]}
                >
                    {({ features}) =>
                        features
                            .filter(f => includeIds.includes(f.feature.id))
                            .map(({ feature, path }) => {
                                const item = itemMap[feature.id];

                                // eslint-disable-next-line react-hooks/rules-of-hooks
                                const [{ animColor }, setAnim] = useSpring(() => ({
                                    animColor: item.color
                                }));

                                setAnim({
                                    animColor: item.color
                                });
                                return makeSelectableAndInteractive([item.name],
                                    <animated.path
                                        d={path || ''}
                                        fill={animColor.to(x => x)}
                                        stroke={borderColor}
                                        strokeWidth={1}
                                    />, opacityOptions
                                )
                            })
                    }
                </AlbersUsa>
            </svg>
            <Tooltip />
        </>
    );
};

const MapPlot = (props: MapPlotProps) => {
    const year = useAppSelector(selectYear);
    const divisions = useAppSelector(selectCurrentDivisions);
    return year && divisions &&
      <DumbMapPlot {...props} year={year} divisions={divisions} />
}

export default MapPlot;
