import {createSelector} from "@reduxjs/toolkit";
import {
    selectAreas,
    selectDivisionsByLevel,
    selectFields,
    selectLevels,
    selectNumericFields,
    selectValuesByFieldAndDivision,
    selectYearsByLevelAndField
} from "../features/data/dataSlice";
import {intersectAll, invertNestedObject} from "./utils/misc";
import {selectActualControlValues} from "../features/controls/controlsSlice";
import {mapObjIndexed} from "ramda";

export const selectControlValues = createSelector(
    selectActualControlValues,
    selectNumericFields,
    (cv, nfs) => {
        if (!nfs || nfs.length === 0) {
            return cv;
        }
        return {
            ...cv,
            x: cv.x || nfs[0].id,
            y: cv.y || nfs[1].id,
            z: cv.z || nfs[2].id
        }
    }
);

export const selectXyzControlValues = createSelector(
    selectControlValues,
    (cv) => ({
        x: cv.x,
        y: cv.y,
        z: cv.z
    }),
)

export const selectNonNullXyzControlArray = createSelector(
    selectXyzControlValues,
    (cv) => Object.values(cv).filter(v => !!v)
);

export const selectControlFields = createSelector(
    selectAreas,
    selectLevels,
    selectFields,
    selectControlValues,
    (areas, levels, fields, values) => {
        return mapObjIndexed((v, k) => {
            if (!fields || !v) {
                return null;
            }
            if (k === 'area') {
                return areas[v];
            }
            if (k === 'compare') {
                return levels[v];
            } else {
                return fields[v];
            }
        }, values)
    }
);

export const selectCurrentDivisions = createSelector(
    selectDivisionsByLevel,
    selectControlValues,
    (dvsByLevel, cv): any[] =>
        dvsByLevel[cv.compare]
);

export const selectYears = createSelector(
    selectYearsByLevelAndField,
    selectControlValues,
    selectNonNullXyzControlArray,
    (yblaf, cvs, xyz) => {
        if (Object.keys(yblaf).length > 0) {
            return intersectAll(xyz.map(cv => yblaf[cvs.compare][cv]))
        } else {
            return [];
        }
    }
);


const processDatasetItem = (vbfad, field) => {
    return field ? invertNestedObject(vbfad[field]) : null
};

export const selectCurrentDataset = createSelector(
    selectValuesByFieldAndDivision,
    selectControlValues,
    (vbfad, cv) => ({
        byField: {
            x: vbfad[cv.x],
            y: vbfad[cv.y],
            z: vbfad[cv.z]
        },
        byYear: invertNestedObject({
                x: processDatasetItem(vbfad, cv.x),
                y: processDatasetItem(vbfad, cv.y),
                z: processDatasetItem(vbfad, cv.z)

        })
    })
);
