import React from 'react';
import { useAppSelector, useAppDispatch } from '../../app/hooks';
import { setYear, selectYear } from './yearSlice';
import {Box, Slider} from "@mui/material";
import {styled} from "@mui/system";
import {selectPlotType} from "../plotType/plotTypeSlice";
import {last} from 'ramda';
import {selectYears} from "../../dashboard/selectors";

const PrettoSlider = styled(Slider)({
    color: '#318Cf6',
    height: 8,
    '& .MuiSlider-track': {
        border: 'none',
    },
    '& .MuiSlider-thumb': {
        height: 24,
        width: 24,
        backgroundColor: '#fff',
        border: '2px solid currentColor',
        '&:focus, &:hover, &.Mui-active, &.Mui-focusVisible': {
            boxShadow: 'inherit',
        },
        '&:before': {
            display: 'none',
        },
    },
    '& .MuiSlider-valueLabel': {
        lineHeight: 1.2,
        fontSize: 12,
        background: 'unset',
        padding: 0,
        width: 32,
        height: 32,
        borderRadius: '50% 50% 50% 0',
        backgroundColor: '#318Cf6',
        transformOrigin: 'bottom left',
        transform: 'translate(50%, -100%) rotate(-45deg) scale(0)',
        '&:before': { display: 'none' },
        '&.MuiSlider-valueLabelOpen': {
            transform: 'translate(50%, -100%) rotate(-45deg) scale(1)',
        },
        '& > *': {
            transform: 'rotate(45deg)',
        },
    },
    '& .Mui-disabled .MuiSlider-valueLabel': {
        display: 'none'
    }
});

const YearSlider = () => {
    const year = useAppSelector(selectYear);
    const plotType = useAppSelector(selectPlotType);
    const years = useAppSelector(selectYears);
    const dispatch = useAppDispatch();

    return plotType && years && year && (
        <Box sx={{
            flex: '1 1'
        }}>
            <PrettoSlider
                defaultValue={last(years)}
                min={years[0]}
                max={last(years)}
                step={1}
                disabled={plotType.name === 'line'}
                valueLabelDisplay="on"
                onChange={(event, y: number) => {
                    if (year !== y) {
                        dispatch(setYear(y))
                    }
                }}
                sx={{
                    paddingTop: '20px',
                    '& .MuiSlider-markLabel': {
                        top: '40px'
                    }
                }}
            />
        </Box>
    );
}

export default YearSlider;
