import {invertMapOfMaps} from "./misc";
import {toPairs, map} from 'ramda';

export const prepareData = (dataByYear, year) => {
    const dataArray = invertMapOfMaps(dataByYear[year]);
    return toPairs(map(v => ({
        ...v,
        year
    }), dataArray));
};

export const allValuesNotNull = (obj: Object) : boolean =>
    Object.values(obj).every(f => f != null);
