import React from "react";
import useMediaQuery from '@mui/material/useMediaQuery';
import {TransitionProps} from "@mui/material/transitions";
import {
    Button,
    Collapse,
    Dialog,
    DialogActions,
    DialogContent,
    FormControl,
    FormControlLabel,
    FormGroup,
    IconButton,
    InputLabel,
    NativeSelect,
    Switch,
    TextField
} from "@mui/material";
import {
    FacebookIcon,
    FacebookShareButton,
    LinkedinIcon,
    LinkedinShareButton,
    PinterestIcon,
    PinterestShareButton,
    RedditIcon,
    RedditShareButton,
    TwitterIcon,
    TwitterShareButton,
} from "react-share";
import FileCopyOutlinedIcon from '@mui/icons-material/FileCopyOutlined';
import * as htmlToImage from 'html-to-image';
import {
    AxisLabelSource, PlotType,
    SHARE_SIZE_MAP,
    SHARE_SIZES,
    ShareSize
} from "./config";
import {CopyToClipboard} from 'react-copy-to-clipboard';
import {useAppSelector} from "../app/hooks";
import {selectPlotType} from "../features/plotType/plotTypeSlice";
import {selectYear} from "../features/year/yearSlice";
import {selectCurrentDivisions} from "./selectors";
import './sharing.scss';
import {useTheme} from "@mui/system";
import {compose} from 'ramda';

const ZOOM = 1;
const MAX_PREVIEW_WIDTH = 208 / ZOOM;
const MAX_PREVIEW_HEIGHT = 208 / ZOOM;

const calculatePreviewSize = ({width, height}) => {
    const ratio = height / width;
    const h1 = Math.min(ratio * MAX_PREVIEW_WIDTH, MAX_PREVIEW_HEIGHT);
    const w = Math.min(h1 / ratio, MAX_PREVIEW_WIDTH);
    const h = Math.min(w * ratio);
    return {width: w, height: h};
};

const ShareTransition = React.forwardRef(function Transition(
    props: TransitionProps & { children?: React.ReactElement<any, any> },
    ref: React.Ref<unknown>,
) {
    return <Collapse ref={ref} {...props} timeout={500} />;
});

const BaseDialog = ({setOpen, children, fullScreen=false}) => {
    React.useEffect(() => {
        window.location.hash = "#dialog";
        const onHashChange = () => setOpen(window.location.hash === "#dialog");
        window.addEventListener("hashchange", onHashChange);
        return () => window.removeEventListener("hashchange", onHashChange);
    }, []);

    return (
        <Dialog
            className='share-dialog'
            open={true}
            onClose={() => window.history.back()}
            fullScreen={fullScreen}
            TransitionComponent={ShareTransition}
            keepMounted
        >
            {children}
        </Dialog>
    );
};

const createPlotTitle = (plotType: PlotType, year: number, compareByVar, xAxisLabel, yAxisLabel) => {
    return (!plotType.xAxisLabelSource || plotType.xAxisLabelSource === AxisLabelSource.COMPARE ?
        yAxisLabel + ' by ' +
        compareByVar.toLowerCase()
        :
        yAxisLabel + ' / ' +
        xAxisLabel.toLowerCase() + ' by ' +
        compareByVar.toLowerCase()) +
        (plotType.name === 'line' ? '' : ` (${year})`)
};

export const DumbImageDialog = ({plot, setOpen, label, setLabel,
                                compareByVar, xAxisLabel, yAxisLabel, showAxisLabels,
                                plotType, year, items}) => {
    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('xs'));
    const plotTitle = createPlotTitle(plotType, year, compareByVar, xAxisLabel, yAxisLabel);
    const defaultShareSize = SHARE_SIZES[0];
    const [sharePresetSize, setSharePresetSize] = React.useState(defaultShareSize);
    const [previewSize, setPreviewSize] = React.useState({
        width: MAX_PREVIEW_WIDTH,
        height: MAX_PREVIEW_HEIGHT
    });
    const [shareSize, setShareSize] = React.useState({
        width: defaultShareSize.width,
        height: defaultShareSize.height
    });
    const [format, setFormat] = React.useState('PNG');
    const [imgData, setImgData] = React.useState<string>(null);

    const canvasToPng = (canvas) => {
        return canvas.toDataURL("image/png", 1.0);
    };

    const getGenerateFn = () => {
        switch (format) {
            case 'PNG': return compose(p => p.then(canvasToPng), htmlToImage.toCanvas);
            case 'SVG': return htmlToImage.toSvg;
            default: throw Error('Unhandled format: ' + format);
        }
    };

    const GenerateDialog = ({setImgData}) => {
        const generate = () => {
            const el: any = document
                .getElementById('preview')
                .getElementsByClassName('gen-target')[0];
            getGenerateFn()(el, {
                canvasWidth: shareSize.width,
                canvasHeight: shareSize.height,
                // width: shareSize.width * 4,
                // height: shareSize.height * 4,
                quality: 1.0,
                style: {
                    // @ts-ignore
                    // zoom: 1
                    // width: shareSize.width * 4 + 'px',
                    // height: shareSize.height * 4 + 'px'
                }
            }).then((dataUrl) => {
                setImgData(dataUrl);
            });
        };

        const setNewSize = (newSize) => {
            setShareSize(newSize);
            setPreviewSize(calculatePreviewSize(newSize));
        };

        const onPresetSizeSelect = (event) => {
            const name = event.target.value;
            if (name === 'Custom') {
                setSharePresetSize(null);
                document
                    .getElementsByClassName('MuiDialog-container')[0]
                    .getElementsByClassName('MuiPaper-root')[0]
                    .scrollTo(0, 9999);
            } else {
                const s = SHARE_SIZE_MAP[name];
                setSharePresetSize(s);
                setNewSize(s);
            }
        };

        const onSizeChange = (newSize) => {
            setSharePresetSize(null);
            setNewSize(newSize);
        };

        const onLabelChange = (event) => {
            const value = event.target.value;
            setLabel(value === ' ' ? null : value);
        };

        const onFormatChange = (event) => {
            const value = event.target.value;
            setFormat(value);
        }

        const fontSize = Math.round(previewSize.width / 40);

        return (
            <>
                <div className='dialog-title'>Generate image</div>
                <DialogContent className='dialog-content'>
                    <div id='preview-container'>
                        <div id='preview' style={{...previewSize, fontSize}}>
                            <div className='gen-target'>
                                <div className='plot-frame'>
                                    <div className='title'>{plotTitle}</div>
                                    <div className='subtitle'>
                                        Statistipedia: statistipedia.org/explore
                                    </div>
                                    {plot}
                                </div>
                                { showAxisLabels && xAxisLabel &&
                                <div className='x-axis'>
                                    {xAxisLabel || 'Year'}
                                </div>
                                }
                                { showAxisLabels && yAxisLabel &&
                                <div className='y-axis'>
                                    {yAxisLabel}
                                </div>
                                }
                            </div>
                        </div>
                        <div className="MuiFormLabel-root caption">
                            Preview
                        </div>
                    </div>
                    <div className='share-controls'>
                        <div className='size-inputs'>
                            <FormControl>
                                <InputLabel variant="standard" htmlFor="preset-size">Size</InputLabel>
                                <NativeSelect id="preset-size"
                                              onChange={onPresetSizeSelect}
                                              value={sharePresetSize ? sharePresetSize.name : 'Custom'}>
                                    <optgroup label="Presets">
                                        {SHARE_SIZES.map((s) => (
                                            <option key={s.name}>{s.name}</option>
                                        ))}
                                    </optgroup>
                                    <option>Custom</option>
                                </NativeSelect>
                            </FormControl>
                            <FormGroup row className='custom-size-inputs top-padded row'>
                                {['width', 'height'].map((s) => (
                                    <FormControl key={s} className='top-padded custom-size-input'>
                                        <TextField helperText={s}
                                                   variant="standard"
                                                   onChange={(event) =>
                                                       onSizeChange({...shareSize, [s]: event.target.value})}
                                                   value={shareSize[s]} />
                                    </FormControl>
                                ))}
                            </FormGroup>
                            <FormGroup row className='row'>
                                <FormControl className="form-control">
                                    <InputLabel variant="standard" htmlFor="label">Labeled item</InputLabel>
                                    <NativeSelect id="label"
                                                  value={label ? label : ' '}
                                                  onChange={onLabelChange}>
                                        <option value=' ' />
                                        {items.map(({name}) => (
                                            <option key={name}>{name}</option>
                                        ))}
                                    </NativeSelect>
                                </FormControl>
                            </FormGroup>
                            <FormGroup row className='top-padded row'>
                                <FormControl className="form-control">
                                    <InputLabel variant="standard" htmlFor="format">Format</InputLabel>
                                    <NativeSelect id="format"
                                                  value={format}
                                                  onChange={onFormatChange}
                                    >
                                        <option>PNG</option>
                                        <option>SVG</option>
                                    </NativeSelect>
                                </FormControl>
                                <FormControl className="form-control">
                                    <InputLabel variant="standard" htmlFor="light_dark">
                                        Mode
                                    </InputLabel>
                                    <NativeSelect id="light_dark" value='Light mode'>
                                        <option>Light</option>
                                        <option>Dark</option>
                                    </NativeSelect>
                                </FormControl>
                            </FormGroup>
                            <FormControl className="form-control">
                                <FormControlLabel
                                    control={
                                        <Switch
                                            color="primary"
                                        />
                                    }
                                    label="Add QR code"
                                />
                            </FormControl>
                        </div>
                    </div>
                </DialogContent>
                <DialogActions className='dialog-actions'>
                    <Button onClick={() => window.history.back()}>Close</Button>
                    <Button onClick={generate}>Generate</Button>
                </DialogActions>
            </>
        );
    };

    const DownloadDialog = ({imgData, setImgData}) => {
        const url = window.location.href;

        // const share = () => {
        //     if (navigator.share) {
        //         navigator.share({
        //             title: 'web.dev',
        //             text: 'Check out web.dev.',
        //             // url: 'https://web.dev/',
        //             files: [image]
        //         })
        //             .then(() => alert('Successful share'))
        //             .catch((error) => alert(error));
        //     }
        // };
        return (
            <>
                <div className='dialog-title'>Download image</div>
                <DialogContent className='dialog-content'>
                    <div className='generated-image'>
                        <a download={plotTitle} href={imgData}>
                            <img alt="Generated" src={imgData} />
                        </a>
                    </div>
                    <div className='share-controls'>
                        <div className='image-share-btns'>
                            <div className='image-share-btn'>
                                <PinterestShareButton
                                    url={url}
                                    media={imgData}
                                >
                                    <PinterestIcon round />
                                    <div style={{color: '#C9242E'}}>
                                        Share on Pinterest
                                    </div>
                                </PinterestShareButton>
                            </div>
                        </div>
                    </div>
                </DialogContent>
                <DialogActions className='dialog-actions'>
                    <Button onClick={() => setImgData(null)}>Back</Button>
                    <Button onClick={() => window.history.back()}>Close</Button>
                    <Button href={imgData} download={plotTitle}>Download</Button>
                </DialogActions>
            </>
        )
    };
    return (
        <BaseDialog setOpen={setOpen} fullScreen={fullScreen}>
            {imgData
                ? <DownloadDialog imgData={imgData} setImgData={setImgData} />
                : <GenerateDialog setImgData={setImgData} />
            }
        </BaseDialog>
    )
}

export const ImageDialog = (props) => {
    const plotType = useAppSelector(selectPlotType);
    const year = useAppSelector(selectYear);
    const items = Object.values(useAppSelector(selectCurrentDivisions));
    return <DumbImageDialog {...props} plotType={plotType} year={year} items={items} />
};

export const ShareDialog = ({setOpen, compareByVar, xAxisLabel, yAxisLabel}) => {
    const url = window.location.href;
    const plotType = useAppSelector(selectPlotType);
    const year = useAppSelector(selectYear);
    const plotTitle = createPlotTitle(plotType, year, compareByVar, xAxisLabel, yAxisLabel);
    return (
        <BaseDialog setOpen={setOpen}>
            <>
                <div className='dialog-header'>
                    <div className='dialog-title'>Share</div>
                </div>
                <DialogContent className='dialog-content'>
                    <div className='social-btns'>
                        <FacebookShareButton
                            url={url}
                            quote={plotTitle}
                        >
                            <FacebookIcon round />
                        </FacebookShareButton>
                        <TwitterShareButton
                            url={url}
                            title={plotTitle}
                        >
                            <TwitterIcon round />
                        </TwitterShareButton>
                        <RedditShareButton
                            url={url}
                            title={plotTitle}
                        >
                            <RedditIcon round />
                        </RedditShareButton>
                        <LinkedinShareButton url={url}>
                            <LinkedinIcon round />
                        </LinkedinShareButton>
                    </div>
                    <div style={{display: 'flex'}}>
                        <TextField value={url} />
                        <CopyToClipboard onCopy={() => {}} text={url}>
                            <IconButton aria-label="copy"
                                        className='copy-btn'
                                        title='Copy to clipboard'>
                                <FileCopyOutlinedIcon />
                            </IconButton>
                        </CopyToClipboard>
                    </div>
                </DialogContent>
            </>
        </BaseDialog>
    );
};
