import {getUrlParams} from "../../utils";
import axios from 'axios';

export type Params = Map<string, any>

export type DataSplittingConfig = {
    paramsToChunkUrls: (params: Params) => string[],
    onChunkFetch: (chunk: any) => void
}

export const MASTER_CHUNK_FILENAME = 'master.json';
export const CHUNK_DIR = "public/data";
const BASE_CHUNK_URL = "/explore/data";

export const computeChunkUrl = (level: string, feature: string) : string => {
    return 'values.json';
};

export class SplittingDataFetcher {
    private config: DataSplittingConfig;

    constructor(config: DataSplittingConfig) {
        this.config = config;
    }

    private _computeChunkUrls = () : string[] => {
        const urlParams = getUrlParams();
        return this.config.paramsToChunkUrls(urlParams)
            .map(u => BASE_CHUNK_URL + '/' + u);
    };

    private _fetchChunks = (urls: string[]) => {
        axios.all(urls.map(url => axios.get(url)))
            .then(axios.spread((...responses) =>
                responses.map(r => this.config.onChunkFetch(r.data))
            ));
    };

    fetchChunks = () => this._fetchChunks(this._computeChunkUrls());
}

