import {createSlice, PayloadAction} from '@reduxjs/toolkit';
import { RootState } from '../../app/store';
import {getUrlParams} from "../../utils";

export interface ControlValues {
    area: string,
    compare: string,
    x: string,
    y: string,
    z: string
}

export interface ControlsState {
    value: ControlValues
}

export interface ControlsStateItem {
    key: string
    value: string
}

const urlParams = getUrlParams();

const initialState: ControlsState = {
    value: {
        area: "us",
        compare: "state",
        x: urlParams.x,
        y: urlParams.y,
        z: urlParams.z
    }
};

export const controlsSlice = createSlice({
    name: 'controls',
    initialState,
    // The `reducers` field lets us define reducers and generate associated actions
    reducers: {
        // Use the PayloadAction type to declare the contents of `action.payload`
        setControl: (state, action: PayloadAction<ControlsStateItem>) => {
            state.value[action.payload.key] = action.payload.value;
        },
        setControls: (state, action: PayloadAction<Object>) => {
            state.value = {...state.value, ...action.payload};
        }
    }
});

export const { setControl, setControls } = controlsSlice.actions;

export const selectActualControlValues = (state: RootState) : ControlValues => {
    return state.controls.value;
};

export default controlsSlice.reducer;
