import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import {last} from 'ramda';
import { RootState } from '../../app/store';
import {dataSlice} from "../data/dataSlice";
import {Data} from "../../dashboard/config";

export interface YearState {
  value: number;
  status: 'idle' | 'loading' | 'failed';
}

const initialState: YearState = {
  value: null,
  status: 'idle',
};

export const yearSlice = createSlice({
  name: 'year',
  initialState,
  reducers: {
    setYear: (state, action: PayloadAction<number>) => {
      const value = action.payload;
      if (value != null && value !== state.value) {
          state.value = value;
      }
    }
  },
  extraReducers: {
    // @ts-ignore
    [dataSlice.actions.setData]: (state, action: PayloadAction<Data>) => {
      const data = action.payload;
      // TODO: I'd like to use the state but it's not set in the store yet.
      // TODO: Also selectors cannot be run from reducers?
      // TODO: That'd be the preferred way wouldn't it?
      if (data.years) {
        // TODO: We need to know what level and field it actually has
        // TODO: which might pose a problem because we don't know that
        // TODO: this reducer runs after the one in controlsSlice.
        const level = "state";
        const field = "total_pop";
        state.value = last(data.years.byLevelAndField[level][field]);
      }
    }
  }
});

export const { setYear } = yearSlice.actions;
export const selectYear = (state: RootState) => state.year.value;
export default yearSlice.reducer;
