import React from 'react';
import { Tooltip } from '@visx/tooltip';
import { withBoundingRects } from '@visx/bounds';

function CustomTooltipWithBounds({
                                     left: initialLeft,
                                     top: initialTop,
                                     offsetLeft = 20,
                                     offsetTop = 20,
                                     rect,
                                     parentRect,
                                     getRects,
                                     children,
                                     style,
                                     ...otherProps
                                 }) {

    let left = initialLeft;
    let top = initialTop;

    if (parentRect) {
        if (parentRect.width > 0) {
            if (left + offsetLeft + rect.width > parentRect.width) {
                left = parentRect.width - rect.width - offsetLeft;
            }
            if (left < 0) {
                left = 0;
            }
        }

        if (parentRect.height > 0) {
            if (top + offsetTop + rect.height > parentRect.height) {
                top = parentRect.height - rect.height - offsetTop;
            }
            if (top < 0) {
                top = 0;
            }
        }
    }

    return (
        <Tooltip
            style={{ top: 0, transform: `translate(${left}px, ${top}px)`, ...style }}
            {...otherProps}
        >
            {children}
        </Tooltip>
    );
}
export default withBoundingRects(CustomTooltipWithBounds);
