import React from 'react';
import { Provider } from 'react-redux';
import { store } from '../app/store';
import {
    AppBar,
    Box,
    Card,
    CircularProgress,
    Collapse,
    CssBaseline,
    Divider,
    Drawer,
    Hidden,
    IconButton,
    List,
    ListItem,
    ListItemIcon,
    ListItemText,
    ThemeProvider,
    Toolbar,
    Typography,
    useMediaQuery
} from '@mui/material';
import { createTheme } from '@mui/material/styles';
import BubblePlot from "./BubblePlot";
import {ParentSize} from "@visx/responsive";
import './Dashboard.scss';
import MenuIcon from "@mui/icons-material/Menu";
import HomeIcon from "@mui/icons-material/Home";
import DataIcon from "@mui/icons-material/Storage";
import ChartIcon from "@mui/icons-material/InsertChart";
import PlayIcon from '@mui/icons-material/PlayArrow';
import ShareIcon from '@mui/icons-material/Share';
import PhotoCameraIcon from '@mui/icons-material/PhotoCamera';
import ExpandLess from '@mui/icons-material/ExpandLess';
import ExpandMore from '@mui/icons-material/ExpandMore';
import {AxisLabelSource, IDashboardProps} from "./config";
import {setUseWhatChange, useWhatChanged} from '@simbathesailor/use-what-changed';
import MapPlot from "./MapPlot";
import LinePlot from "./LinePlot";
import BarPlot from "./BarPlot";
import {ImageDialog, ShareDialog} from "./sharing";
import YearSlider from "../features/year/YearSlider";
import {useAppDispatch, useAppSelector} from "../app/hooks";
import {selectPlotType} from "../features/plotType/plotTypeSlice";
import Controls from "../features/controls/Controls";
import DataManager from "../features/data/DataManager";
import Url from './Url';
import {setControl} from "../features/controls/controlsSlice";
import {selectControlFields, selectCurrentDataset} from "./selectors";
import { QueryParamProvider } from 'use-query-params';
import {allValuesNotNull} from "./utils/domain";

setUseWhatChange(process.env.NODE_ENV === 'development');
require('typeface-roboto');

const LoadingPlot = ({width, height}) => (
    <div className='loading-plot' style={{width, height}}>
        <CircularProgress variant='indeterminate'
                          size={Math.min(Math.round(width / 7), 80)} />
    </div>
);

const PlotArea = ({data, loading, plotType,
                      axisLabels, xAxisLabel=null, yAxisLabel=null,
                      disableInteraction = false, showLabel,
                      x, y, sizeVar}) => {
    const showPlot = (width: number, height: number) => {
        if (!data)
            return null;
        switch (plotType.name) {
            case 'bubble': return <BubblePlot data={data}
                                              showLabel={showLabel}
                                              disableInteraction={disableInteraction}
                                              width={width}
                                              height={height} />;
            case 'line': return <LinePlot data={data}
                                          showLabel={showLabel}
                                          disableInteraction={disableInteraction}
                                          width={width}
                                          height={height} />;
            case 'bar': return <BarPlot data={data}
                                        showLabel={showLabel}
                                        disableInteraction={disableInteraction}
                                        width={width}
                                        height={height} />;
            default: return <MapPlot data={data}
                                     showLabel={showLabel}
                                     disableInteraction={disableInteraction}
                                     width={width}
                                     height={height}
                                     yAxisVar={y} />;
        }
    };
    return (
        <ParentSize debounceTime={10} className='bg'>
            {({ width, height }) => {
                const fontSize = Math.min(Math.round(width / 18), 16);
                return (width + height) > 0 && (
                    <div className='plot' style={{width, height, fontSize}}>
                        {loading &&
                        <LoadingPlot width={width} height={height}/>}
                        {showPlot(width, height)}
                        {axisLabels && !plotType.hideAxisLabels && (
                            <>
                                <div className='x-axis'>
                                    {xAxisLabel || 'Year'}
                                </div>
                                <div className='y-axis' style={{width: height}}>
                                    {yAxisLabel}
                                </div>
                            </>
                        )}
                    </div>
                )
            }}
        </ParentSize>
    );
};

const SideMenuItem = ({text, icon, children=null}) => {
    const [open, setOpen] = React.useState(false);
    return (
        <>
            <ListItem button onClick={() => setOpen(!open)}>
                <ListItemIcon className='menu-item-icon'>{icon}</ListItemIcon>
                <ListItemText primary={text} />
                {children && (open ? <ExpandLess /> : <ExpandMore />)}
            </ListItem>
            {children && (
                <Collapse in={open} timeout="auto" unmountOnExit className='nested-menu'>
                    {children}
                </Collapse>
            )}
        </>
    );
};

type DumbDashboardProps = {
    plotType: any
    controls: any
}

const DumbDashboard = ({plotType, controls}: DumbDashboardProps) => {
    const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)');

    const computeXAxisLabel = () : string => {
        switch (plotType.xAxisLabelSource) {
            case AxisLabelSource.VAR: return controls.x.name;
            case AxisLabelSource.COMPARE: return controls.compare.name;
            case AxisLabelSource.YEAR: return 'Year'
            default: return null;
        }
    };

    const xAxisLabel = computeXAxisLabel();
    const yAxisLabel = controls.y?.name;

    const theme = React.useMemo(
        () =>
            createTheme({
                palette: {
                    mode: prefersDarkMode ? 'dark' : 'light',
                    primary: {
                        main: prefersDarkMode ? '#175391' : '#2278CF'
                    }
                },
                breakpoints: {
                    values: {
                        xs: 360,
                        sm: 430,
                        md: 640,
                        lg: 960,
                        xl: 1080,
                    },
                }
            }),
        [prefersDarkMode],
    );

    const Plot = ({axisLabels, showLabel = null, disableInteraction = false}) => {
        const data = useAppSelector(selectCurrentDataset);
        return controls.y && (
            <PlotArea data={data}
                      loading={!data}
                      plotType={plotType}
                      axisLabels={axisLabels}
                      disableInteraction={disableInteraction}
                      showLabel={showLabel}
                      x={controls.x}
                      y={controls.y}
                      xAxisLabel={xAxisLabel}
                      yAxisLabel={yAxisLabel}
                      sizeVar={controls.size}
            />
        );
    };

    const SideMenu = () => {
        const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)');
        const [drawerOpen, setDrawerOpen] = React.useState(false);
        const [genImgOpen, setGenImgOpen] = React.useState(false);
        const [shareOpen, setShareOpen] = React.useState(false);
        const [showLabel, setShowLabel] = React.useState(null);

        const drawer = (
            <div className='side-menu'>
                <Divider />
                <List>
                    <a href='/'>
                        <SideMenuItem text='Statistpedia' icon={<HomeIcon />} />
                    </a>
                    <SideMenuItem text='Datasets' icon={<DataIcon />} />
                    <SideMenuItem text='Presets' icon={<ChartIcon />}>
                        <List>
                            <ListItem button>
                                <ListItemText>
                                    Home prices vs rents
                                </ListItemText>
                            </ListItem>
                            <ListItem button>
                                <ListItemText>
                                    Income by sex
                                </ListItemText>
                            </ListItem>
                        </List>
                    </SideMenuItem>
                </List>
                <Divider />
            </div>
        );
        return (
            <>
                <nav>
                    <Hidden xlUp>
                        <Drawer
                            variant="temporary"
                            open={drawerOpen}
                            onClose={() => setDrawerOpen(false)}
                            ModalProps={{
                                keepMounted: true, // Better open performance on mobile.
                            }}
                        >
                            {drawer}
                        </Drawer>
                    </Hidden>
                    <Hidden xlDown>
                        <Drawer
                            variant="permanent"
                            open
                        >
                            {drawer}
                        </Drawer>
                    </Hidden>
                </nav>
                <AppBar className='top-bar'
                        position="static"
                        elevation={prefersDarkMode ? 0 : 1}>
                    <Toolbar variant="dense">
                        <IconButton edge="start"
                                    sx={{
                                        marginRight: theme.spacing(2),
                                        display: { xs: 'inline-flex', xl: 'none' }
                                    }}
                                    onClick={() => setDrawerOpen(!drawerOpen)}
                                    color="inherit"
                                    aria-label="menu">
                            <MenuIcon />
                        </IconButton>
                        <Typography
                            variant="h6"
                            sx={{
                                display: { xs: 'none', sm: 'block' }
                            }}
                        >
                            Statistipedia Explore
                        </Typography>
                        <div className="grow" />
                        <IconButton className='menu-btn'
                                    onClick={() => setGenImgOpen(true)}
                                    color="inherit">
                            <PhotoCameraIcon />
                        </IconButton>
                        <IconButton edge="end"
                                    className='menu-btn'
                                    onClick={() => setShareOpen(true)}
                                    color="inherit">
                            <ShareIcon />
                        </IconButton>
                    </Toolbar>
                </AppBar>
                {genImgOpen && <ImageDialog
                                    setOpen={setGenImgOpen}
                                    label={showLabel}
                                    setLabel={setShowLabel}
                                    compareByVar={controls.compare.name}
                                    xAxisLabel={xAxisLabel}
                                    yAxisLabel={yAxisLabel}
                                    showAxisLabels={!plotType.hideAxisLabels}
                                    plot={<Plot axisLabels={false}
                                                disableInteraction={true}
                                                showLabel={showLabel} />} />}
                {shareOpen && <ShareDialog
                                  compareByVar={controls.compare.name}
                                  xAxisLabel={xAxisLabel}
                                  yAxisLabel={yAxisLabel}
                                  setOpen={setShareOpen} />}
            </>
        );
    };

    useWhatChanged([plotType, controls]);

    return (
        <ThemeProvider theme={theme}>
            <CssBaseline />
            <Box sx={{
                boxSizing: 'border-box',
                width: '100%',
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                paddingLeft: { xs: 0, xl: '270px' }
            }}>
                <SideMenu />
                <div id="dashboard">
                    <main>
                        <div className='plot-area'>
                            <Card className='section'>
                                <div id='inner-plot-container'>
                                    <Plot axisLabels={true} />
                                </div>
                            </Card>
                        </div>
                        <div className='timeline'>
                            <IconButton className='play' disabled={plotType.name === 'line'}>
                                <PlayIcon />
                            </IconButton>
                            <YearSlider />
                        </div>
                    </main>
                    <aside>
                        <Controls />
                    </aside>
                </div>
            </Box>
        </ThemeProvider>
    );
};

const myHistory = {
    // eslint-disable-next-line no-restricted-globals
    push: (location: Location) => history.pushState({}, null, location.href),
    // eslint-disable-next-line no-restricted-globals
    replace: (location: Location) => history.replaceState({}, null, location.href),
};

const Dashboard = () => {
    const plotType = useAppSelector(selectPlotType);
    const controls = useAppSelector(selectControlFields);
    const controlsIsReady = allValuesNotNull(controls);
    return (
        <>
            {
                controlsIsReady && plotType &&
                <DumbDashboard plotType={plotType} controls={controls} />
            }
            <DataManager />
            <QueryParamProvider history={myHistory}>
                <Url />
            </QueryParamProvider>
        </>
    );
}

const DashboardApp = () => (
    <Provider store={store}>
        <Dashboard />
    </Provider>
)

export default DashboardApp;
