import React from 'react';
import {
    computeChunkUrl,
    SplittingDataFetcher,
    MASTER_CHUNK_FILENAME
} from "../../dashboard/utils/dataSplitting";
import {useAppDispatch} from "../../app/hooks";
import {setData} from "./dataSlice";

const DataManager = () => {
    const dispatch = useAppDispatch();

    React.useEffect(() => {
        const paramsToChunkUrls = (params) : string[] => {
            return [
                MASTER_CHUNK_FILENAME,
                computeChunkUrl('', '')
            ];
        };

        const onChunkFetch = (chunk: any): void => {
            dispatch(setData(chunk));
        };

        const dataSplitter = new SplittingDataFetcher({
            paramsToChunkUrls,
            onChunkFetch
        });
         dataSplitter.fetchChunks();
    }, [dispatch]);

    return null;
}

export default DataManager;
