import PlotTypeSelect from "../plotType/PlotTypeSelect";
import {
    Autocomplete,
    FormControl,
    InputLabel,
    NativeSelect,
    TextField
} from "@mui/material";
import React from "react";
import {useAppDispatch, useAppSelector} from "../../app/hooks";
import {setControl} from "./controlsSlice";
import {selectPlotType} from "../plotType/plotTypeSlice";
import {selectAreas, selectNumericFields} from "../data/dataSlice";
import {selectControlFields} from "../../dashboard/selectors";
import {allValuesNotNull} from "../../dashboard/utils/domain";

const Controls = () => {
    const plotType = useAppSelector(selectPlotType);
    const controls = useAppSelector(selectControlFields);
    const controlsIsReady = allValuesNotNull(controls);
    const numericFields = useAppSelector(selectNumericFields);
    const areas = useAppSelector(selectAreas);
    const dispatch = useAppDispatch();

    const updateState = (newState: {[key: string]: string}) => {
        const s = {
            key: Object.keys(newState)[0],
            value: Object.values(newState)[0]
        };
        return dispatch(setControl(s))
    };

    return controlsIsReady && Object.keys(areas).length > 0 && (
        <div className='controls'>
            <PlotTypeSelect />
            <Autocomplete
                id="area"
                className="form-control"
                options={Object.keys(areas)}
                value={controls.area.id}
                getOptionLabel={(option) => areas[option].name}
                renderInput={(params) =>
                    <TextField variant="standard" label='Area' {...params} />}
            />
            <FormControl className="form-control">
                <InputLabel variant="standard" htmlFor="compare-by">Compare by</InputLabel>
                <NativeSelect id="compare-by" value={controls.compare.id}
                              onChange={(event) =>
                                  updateState({compare: event.target.value})}
                >
                    <optgroup label='Area'>
                        <option value='state'>State</option>
                        <option>County</option>
                        <option disabled={true}>Place</option>
                    </optgroup>
                    <optgroup label='Demographics'>
                        <option>Sex</option>
                        <option>Race</option>
                        <option>Age</option>
                    </optgroup>
                </NativeSelect>
            </FormControl>
            <FormControl className="form-control"
                         disabled={!plotType.fields.x}>
                <InputLabel variant="standard" htmlFor="x-axis">{plotType.fields.x || 'X-Axis'}</InputLabel>
                <NativeSelect id="x-axis" value={controls.x.id}
                              onChange={(event) =>
                                  updateState({x: event.target.value})}
                >
                    {numericFields.map(f => (
                        <option key={f.id} value={f.id}>{f.name}</option>
                    ))}
                </NativeSelect>
            </FormControl>
            <FormControl className="form-control"
                         disabled={!plotType.fields.y}>
                <InputLabel variant="standard" htmlFor="y-axis">{plotType.fields.y || 'Y-Axis'}</InputLabel>
                <NativeSelect id="y-axis" value={controls.y.id}
                              onChange={(event) =>
                                  updateState({y: event.target.value})}
                >
                    {numericFields.map(f => (
                        <option key={f.id} value={f.id}>{f.name}</option>
                    ))}
                </NativeSelect>
            </FormControl>
            <FormControl className="form-control"
                         disabled={!plotType.fields.z}>
                <InputLabel variant="standard" htmlFor="size">{plotType.fields.z || 'Size'}</InputLabel>
                <NativeSelect id="z" value={controls.z.id}
                              onChange={(event) =>
                                  updateState({z: event.target.value})}
                >
                    {numericFields.map(f => (
                        <option key={f.id} value={f.id}>{f.name}</option>
                    ))}
                </NativeSelect>
            </FormControl>
        </div>
    );
};

export default Controls;
