import SvgIcon from "@mui/material/SvgIcon/SvgIcon";
import BubbleChartIcon from '@mui/icons-material/BubbleChart';
import TimelineIcon from '@mui/icons-material/Timeline';
import BarChartIcon from '@mui/icons-material/BarChart';
import RoomIcon from '@mui/icons-material/Room';
import {zipObj, range} from "ramda";
import {RouteComponentProps} from "@reach/router";

export enum AxisLabelSource {
    VAR,
    COMPARE,
    YEAR
}

export type PlotType = {
    name: string,
    icon: typeof SvgIcon,
    xAxisLabelSource?: AxisLabelSource,
    hideAxisLabels?: boolean,
    fields: {
        x?: string,
        y?: string,
        z?: string
    }
};

export const PLOT_TYPES: PlotType[] = [{
    name: 'bubble',
    icon: BubbleChartIcon,
    xAxisLabelSource: AxisLabelSource.VAR,
    fields: {
        x: 'X-Axis',
        y: 'Y-Axis',
        z: 'Size'
    }
}, {
    name: 'line',  // aka. time series chart
    icon: TimelineIcon,
    xAxisLabelSource: AxisLabelSource.YEAR,
    fields: {
        // X-Axis is always time. Existing selection is kept, only disabled
        y: 'Y-Axis'
    }
}, {
    name: 'bar',
    icon: BarChartIcon,
    xAxisLabelSource: AxisLabelSource.COMPARE,
    fields: {
        // compare: 'Compare by',  // Secondary grouping nested under "Compare by"
        y: 'Y-Axis'
    }
}, {
    name: 'map',
    icon: RoomIcon,
    hideAxisLabels: true,
    fields: {
        y: 'Color'
    }
}];

export const PLOT_TYPES_MAP: Map<string, PlotType> =
    zipObj(PLOT_TYPES.map(p => p.name), PLOT_TYPES);

