export interface IDashboardProps extends RouteComponentProps {
    area?: string;
    compare?: string
}

export enum FieldType {
    NUMERIC,
    CURRENCY
}

export type DataFieldCfg = {
    id: string,
    type?: FieldType
};

export type DataField = {
    id: string,
    name: string,
    type?: FieldType
};

export const fieldCfgs: {[id: string]: {type: FieldType}} = {
    'median_price': {
        type: FieldType.CURRENCY
    },
    'median_rent': {
        type: FieldType.CURRENCY
    },
    'total_pop': {
        type: FieldType.NUMERIC
    }
};

export type Data = {
    areas: {[id: string]: { id: string, name: string }},
    levels: {[id: string]: { id: string, name: string }},
    fields: {[id: string]: DataField},
    years: { byLevelAndField: {[levelId: string]: {[fieldId: string]: any[]}}}
    divisions: { byLevel: {[levelId: string]: any[]} }
    values: { byFieldAndDivision: {[fieldId: string]: any[]} }
}

