import React from "react";
import {max} from 'd3-array';
import {scaleBand, scaleLinear} from "@visx/scale";
import useSelect from "../hooks/useSelect";
import {animated, useSpring} from 'react-spring';
import {useAppSelector} from "../app/hooks";
import {selectYear} from "../features/year/yearSlice";
import {getNestedValues} from "./utils/misc";
import {selectCurrentDivisions} from "./selectors";
import {prepareData} from "./utils/domain";

const hPaddingPct = 0.015;
const vPaddingPct = 0.02

export type ScatterPlotProps = {
    data: any
    showLabel?: string
    disableInteraction?: boolean
    width: number
    height: number
};

const DumbBarPlot = ({
    data,
    showLabel = null,
    disableInteraction = false,
    width,
    height,
    divisions,
    year
}) => {
    const hPadding = React.useMemo(() =>
        Math.min(width, height) * hPaddingPct,
        [width, height]
    );
    const vPadding = React.useMemo(() =>
        Math.min(width, height) * vPaddingPct,
        [width, height]
    );
    const tempItems = prepareData(data.byYear, year)
        .map(([k, v]) => ({
            ...divisions[k],
            ...v
        }));

    const xScale = React.useMemo(() => scaleBand<string>({
        range: [hPadding, width - hPadding],
        round: false,
        domain: tempItems.map(item => item.name),
        padding: 0.25,
    }), [tempItems, hPadding, width]);
    const yScale = React.useMemo(() => scaleLinear<number>({
        domain: [0, max(getNestedValues(data.byField.y))],
        range: [height - vPadding, vPadding],
        round: true
    }), [data, height, vPadding]);

    const items = tempItems.map(item => ({
        ...item,
        scaledX: xScale(item.name),
        scaledY: yScale(item.y)
    }));

    const {makeSelectableAndInteractive, Tooltip} =
        useSelect(items, ['name'], showLabel, disableInteraction);

    return (
        <>
            <svg width={width} height={height - vPadding}>
                {items.map(item => {
                    // eslint-disable-next-line react-hooks/rules-of-hooks
                    const [{ animY }, setAnim] = useSpring(() => ({
                        animY: item.scaledY
                    }));

                    setAnim({
                        animY: item.scaledY
                    });

                    return makeSelectableAndInteractive([item.name],
                        <animated.rect
                            y={animY.to(x => x)}
                            height={animY.to(x => height - x)}
                            x={item.scaledX}
                            width={xScale.bandwidth()}
                            fill='#318Cf6'
                        />
                    )
                })}
            </svg>
            <Tooltip />
        </>
    );
};

const BarPlot = (props: ScatterPlotProps) => {
    const year = useAppSelector(selectYear);
    const divisions = useAppSelector(selectCurrentDivisions);
    return year && divisions &&
      <DumbBarPlot {...props} year={year} divisions={divisions} />
}

export default BarPlot;
