import React from 'react';
import { LinePath } from '@visx/shape';
import {extent} from 'd3-array';
import {scaleLinear} from "@visx/scale";
import {GlyphCircle} from '@visx/glyph';
import { localPoint } from '@visx/event';
import { voronoi } from '@visx/voronoi';
import {groupBy, curry} from "ramda";
import useSelect from "../hooks/useSelect";
import {useAppSelector} from "../app/hooks";
import {selectCurrentDivisions} from "./selectors";
import {mapObjIndexed} from "ramda";

const paddingPct = 0.015;

export type ScatterPlotProps = {
    data: any
    showLabel?: string
    disableInteraction?: boolean
    width: number
    height: number
};

const lineOptions = {
    selectedOpacity: 0.9,
    unselectedOpacity: 0.15,
    defaultOpacity: 0.6
};

const pointOptions = {
    selectedOpacity: 1,
    unselectedOpacity: 0,
    defaultOpacity: 0,
    animate: false
};

const convertData = (ds: Object) : any[] => {
    const a = mapObjIndexed((yvs, id) =>
            mapObjIndexed((y, year) => ({
                id,
                year,
                y
            }), yvs)
        , ds);
    return Object.values(a).flatMap(a2 => Object.values(a2));
};

const DumbLinePlot = ({
    data,
    showLabel = null,
    disableInteraction = false,
    width,
    height,
    divisions
}) => {
    const padding = React.useMemo(() =>
            Math.min(width, height) * paddingPct,
        [width, height]
    );
    const dataArray = convertData(data.byField.y);

    const xScale = scaleLinear<number>({
        domain: extent(dataArray, d => +d.year),
        range: [padding, width - padding],
        round: true
    });
    const yScale = scaleLinear<number>({
        domain: extent(dataArray, d => +d.y),
        range: [height - padding, padding],
        round: true
    });
    const items = dataArray
        .map(d => ({
            ...divisions[d.id],
            ...d,
            scaledX: xScale(d.year),
            scaledY: yScale(d.y),
        }));

    const lines = groupBy(d => d.name, items);
    const lineKeys = Object.keys(lines);
    const {makeSelectable, makeSelectableAndInteractive, Tooltip} =
        useSelect(items, ['name', 'year'], showLabel, disableInteraction);
    const scaledX = (d) => d.scaledX;
    const scaledY = (d) => d.scaledY;
    const strokeWidth = Math.max(Math.round(width / 150), 1);
    const circleSize = Math.round(strokeWidth * 16);

    const voronoiInstance = voronoi({
        x: scaledX,
        y: scaledY,
        width,
        height
    });

    const findNearest = (line, event) => {
        const point = localPoint(event);
        const nearest = voronoiInstance(line).find(point.x, point.y).data;
        return nearest.year;
    };

    return (
        <>
            <svg width={width} height={height}>
                <>
                    {lineKeys.map((key) => {
                        const line = lines[key];
                        return (
                            <g key={key}>
                                {makeSelectableAndInteractive([line[0].name, curry(findNearest)(line)],
                                    <LinePath
                                    data={line}
                                    x={(point: any) => point.scaledX}
                                    y={(point: any) => point.scaledY}
                                    stroke='#3188f6'
                                    strokeWidth={strokeWidth}
                                    />, lineOptions
                                )}
                                {line.map(point => makeSelectable([point.name, point.year],
                                    <GlyphCircle
                                    left={point.scaledX}
                                    top={point.scaledY}
                                    fill='#3188f6'
                                    stroke='#FFFFFF'
                                    size={circleSize}
                                    strokeWidth={Math.max(strokeWidth / 3, 1)}
                                    />, pointOptions
                                ))}
                            </g>
                        );
                    })}
                </>
            </svg>
            <Tooltip />
        </>
    );
};

const LinePlot = (props: ScatterPlotProps) => {
    const divisions = useAppSelector(selectCurrentDivisions);
    return divisions && <DumbLinePlot {...props} divisions={divisions} />;
}

export default LinePlot;
