import React from "react";
import {defaultStyles as defaultTooltipStyles, useTooltip} from "@visx/tooltip";
import {
    all,
    curry,
    groupBy,
    keys,
    last,
    map,
    range,
    reduce,
    tail,
    values
} from "ramda";
import useTouchOutside from "./useTouchOutside";
import TooltipWithBounds from '../dashboard/CustomTooltipWithBounds';
import {animated, useSpring} from "react-spring";
import {useAppSelector} from "../app/hooks";
import {selectPlotType} from "../features/plotType/plotTypeSlice";
import {DataField, FieldType} from "../dashboard/config";
import {selectControlFields} from "../dashboard/selectors";

const DEFAULT_OPTIONS = {
    selectedOpacity: 0.8,
    unselectedOpacity: 0.15,
    defaultOpacity: 0.6
};

type ItemMap = Map<string, any[] | ItemMap>;
type KeyValue = any | ((event: Event) => any)

const groupByKeys = (keyFields: string[], items: any[]) : ItemMap => {
    const im = groupBy(item => item[keyFields[0]], values(items));
    if (keyFields.length === 1) {
        return im;
    } else {
        return map(curry(groupByKeys)(tail(keyFields)), im);
    }
};

const lookupKeys = (keys: string[], itemMap: ItemMap) => {
    return reduce((im, k) => im[k], itemMap, keys);
};

const lookupItem = (keys: string[], itemMap: ItemMap) => {
    const results = lookupKeys(keys, itemMap);
    if (results.length === 1) {
        return results[0];
    } else {
        throw new Error(`Found ${results.length} results for lookup of ${keys}`);
    }
};

const matchesAllKeys = (keysToMatch: string[], itemKeys: string[]) : boolean => {
    const n = Math.min(keysToMatch.length, itemKeys.length);
    // Keys will be coerced into strings, but they might be originally numbers
    // eslint-disable-next-line eqeqeq
    return all(i => keysToMatch[i] == itemKeys[i], range(0, n));
};

const formatValue = (field: DataField, value) => {
    if (field.type === FieldType.CURRENCY) {
        return '$' + Number(value).toLocaleString();
    } else {
        return Number(value).toLocaleString();
    }
};

const TooltipContent = ({item}) => {
    const controls = useAppSelector(selectControlFields);
    const plotType = useAppSelector(selectPlotType);
    return (
        <div className='tooltip-content'>
            <div className='tooltip-title'>
                <h3>{item.name}</h3>
                <div className='year'>{item.year}</div>
            </div>
            <div className='tooltip-body'>
                <div>{controls.y.name}: {formatValue(controls.y, item.y)}</div>
                {plotType.fields.x && (
                    <div>{controls.x.name}: {formatValue(controls.x, item.x)}</div>
                )}
            </div>
        </div>
    );
};

const useSelect = (items: any[], keyFields: string[],
                   showLabel, disableInteraction = false) => {
    const nKeyFields = keyFields.length;
    const itemMap = groupByKeys(keyFields, items);
    const [selectedKeys, selectKeys] = React.useState<string[]>(null);
    const {
        tooltipOpen,
        tooltipLeft,
        tooltipTop,
        tooltipData,
        showTooltip,
        hideTooltip
    } = useTooltip();

    const select = React.useCallback(
        (itemKeys: string[]) => {
            if (!selectedKeys || !matchesAllKeys(selectedKeys, itemKeys)) {
                selectKeys(itemKeys);
                const item = lookupItem(itemKeys, itemMap);
                showTooltip({
                    tooltipData: <TooltipContent item={item} />,
                    tooltipLeft: item.scaledX,
                    tooltipTop: item.scaledY,
                });
            }
        },
        [showTooltip, itemMap, selectedKeys]
    );

    const deselect = React.useCallback(() => {
        selectKeys(null);
        hideTooltip();
    }, [hideTooltip]);

    const getKeys = (key1) : string[] => {
        switch (nKeyFields) {
            case 1: return [key1];
            case 2: return [key1, last(keys(itemMap[key1]))];
            default: throw new Error("Unsupported # of keys: " + nKeyFields);
        }
    };

    React.useEffect(() => {
        if (showLabel) {
            select(getKeys(showLabel));
        }
    });

    const Tooltip = React.memo(() => {
        const ref = React.useRef(null);
        // On touch outside isn't needed for closing the tooltip,
        // but actually to be able to select the next point (on touchscreen)
        useTouchOutside(ref, deselect);
        return tooltipData ? (
            <>
                {tooltipOpen && tooltipData && (
                    // @ts-ignore
                    <TooltipWithBounds
                        top={tooltipTop}
                        left={tooltipLeft}
                        style={{
                            ...defaultTooltipStyles,
                            fontSize: '1em',
                            padding: '0.6em',
                            margin: '0.75em'
                        }}
                        className='tooltip MuiPaper-elevation5'
                    >
                        <div ref={ref}>
                            {tooltipData}
                        </div>
                    </TooltipWithBounds>
                )}
            </>
        ) : null;
    });

    const makeSelectable = React.useCallback((keyValues: any[], c, options= DEFAULT_OPTIONS) => {
        // eslint-disable-next-line react-hooks/rules-of-hooks
        const [{ animOpacity }, setAnim] = useSpring(() => ({
            animOpacity: options.defaultOpacity,
        }));
        const isSelected = selectedKeys && matchesAllKeys(selectedKeys, keyValues);
        const opacity = isSelected ? options.selectedOpacity : selectedKeys ? options.unselectedOpacity : options.defaultOpacity;
        const animate = options.animate == null || options.animate === true;
        if (animate) {
            setAnim({animOpacity: opacity});
            // return React.cloneElement(c, {
            //     key: keyValues.join('_'),
            //     fillOpacity: animOpacity.to(x => x),
            //     strokeOpacity:  animOpacity.to(x => x)
            // });
            return (
                <animated.g
                    fillOpacity={animOpacity.to(x => x)}
                    strokeOpacity={animOpacity.to(x => x)}
                    key={keyValues.join('_')}
                >
                    {c}
                </animated.g>
            );
        } else {
            return React.cloneElement(c, {
                ...c.props,
                key: keyValues.join('_'),
                fillOpacity: opacity,
                strokeOpacity: opacity
            });
        }
    }, [selectedKeys]);

    const makeInteractive = React.useCallback((keyValues: KeyValue[], c) => {
        if (disableInteraction) {
            return c;
        } else {
            const callback = (event) => {
                const resolvedKeyValues = keyValues.map(k => k instanceof Function ? k(event) : k);
                select(resolvedKeyValues);
            }
            return React.cloneElement(c, {
                ...c.props,
                onTouchStart: callback,
                onMouseMove: callback,
                onMouseLeave: deselect
            });
        }
    }, [select, deselect, disableInteraction]);

    const makeSelectableAndInteractive = React.useCallback(
            (keyValues: KeyValue[], c, options= DEFAULT_OPTIONS) => {
                const concreteKeys = keyValues.filter(k => !(k instanceof Function))
        return makeInteractive(keyValues, makeSelectable(concreteKeys, c, options));
    }, [makeSelectable, makeInteractive]);

    return {makeSelectable, makeInteractive, makeSelectableAndInteractive, Tooltip};
};

export default useSelect;
